body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  padding: 0;
  margin: 0;
  height:100%;
}

body {
  overflow: hidden;
  height: 100%;
}

.app {
  width: 960px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.menu-bar {
  position:absolute;
  right: 64px;
  display:flex;
  list-style-type: none;
  width: 384px;
  text-decoration: none;
}

.menu-bar li {
  width: 96px;
  text-decoration: none;
}

.current-menu {
  text-decoration: underline;
}

.home-content {
  position: absolute;
}

.cta-button {
  background-color: black;
  color:black;
}

.footer {
  position: fixed;
  width: 100%;
  bottom: 0 ;
}

.footer-text {
  width:300px;
  margin: 0 auto;
}

/* VECTOR CHARACTER CLASSES */
.person {
  position: absolute;
}

